import { Icon as Iconify } from "@iconify/react";
import { Box, Card, Container, SvgIcon, Typography } from "@mui/material";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { Outlet, useLocation, useNavigate } from "react-router";

import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import ResponsiveTabBar from "../../../../components/ResponsiveTabBar";
import { TabView } from "../../../../tabs";
import useForm from "../../../../utils/useForm";


const Settings = () => {
    const location = useLocation();
    const navigate = useNavigate();

    const { user, inject } = useAuth();

    const middleware = useMiddleware('Authentication.Authenticated');

    const initialValues = {
        firstName: user.firstName,
        lastName: user.lastName,
        ...Object.assign({}, ...(inject('Authentication', 'Settings.initialValues') || [])),
    }

    const { enqueueSnackbar } = useSnackbar();
    const form = useForm({
        initialValues,
        handlePost: data => middleware.updateSettings(data),
        onSubmit: () => {
            enqueueSnackbar('تنظیمات حساب بروزسانی گردید.');
        }
    })

    const task = useTask('Authentication.Settings');

    const injectedHooks = Object.assign({}, ...(inject('Authentication', 'Settings.Hooks') || []));


    return (
        <TabView {...task}>
            <ResponsiveContainer fullHeight>
                <Container disableGutters>
                    <Card
                        variant="outlined"
                        sx={{
                            flexGrow: 1,
                            display: 'flex',
                            flexDirection: 'column'
                        }}
                    >
                        <Box
                            sx={{
                                display: "flex",
                                alignItems: 'start',
                                justifyContent: 'start',
                                p: { xs: 2, sm: 3 }
                            }}
                        >
                            <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
                                {/* {inject('People', 'PersonDetails.PreHeader', { person, loading, reload, params, ...injectedHooks })} */}

                                <Box sx={{ flexGrow: 1 }}>
                                    <Typography variant="h5" sx={{ fontWeight: 600, mb: 1 }}>
                                        تنظیمات حساب
                                    </Typography>
                                </Box>
                            </Box>
                        </Box>

                        <ResponsiveTabBar
                            tabs={sortBy([
                                {
                                    label: 'مشخصات کاربری',
                                    value: `/account/settings`,
                                    icon: <SvgIcon component={Iconify} icon="solar:user-id-bold-duotone" />,
                                    priority: 100
                                },
                                {
                                    label: 'امنیت',
                                    value: `/account/settings/security`,
                                    icon: <SvgIcon component={Iconify} icon="solar:folder-security-bold-duotone" />,
                                    priority: 200
                                },
                                ...(inject('Authentication', 'Settings.TabsList', { ...injectedHooks }) || [])
                            ].map(o => ({
                                ...o, onClick: () => navigate(
                                    o.value,
                                    { state: { prev: location }, replace: true }
                                )
                            })), o => o.priority)}
                            value={location.pathname}
                        >
                            <Outlet context={{ form }}
                            />
                        </ResponsiveTabBar>
                    </Card>
                </Container>

                {/* {inject('People', 'PersonDetails.Footer', { person, loading, reload, params, ...injectedHooks })} */}
            </ResponsiveContainer>
        </TabView>
    )
}

export default Settings;